import {useRadio, useRadioGroup, HStack, Box} from "@chakra-ui/react";
import React, {useState} from "react";

// 1. useRadio 훅을 사용하는 컴포넌트 생성
function RadioCard(props) {
    const { getInputProps, getCheckboxProps } = useRadio(props)

    const input = getInputProps()
    const checkbox = getCheckboxProps()

    return (
        <Box as='label'>
            <input {...input} />
            <Box
                {...checkbox}
                cursor='pointer'
                borderWidth='1px'
                borderRadius='md'
                boxShadow='md'
                _checked={{
                    bg: '#4BD593',
                    color: 'white',
                    borderColor: '#4BD593',
                }}
                _focus={{
                    boxShadow: 'outline',
                }}
                px={5}
                py={2}
            >
                {props.children}
            </Box>
        </Box>
    )
}

// 2. useRadioGroup 훅을 이용하여 커스텀 라디오 그룹 조작
export default function ControlDesignRadio(props) {
    const options = ['지화1', '지화2', '지화3']

    const handleDesignChange = (e) => {
        props.setDesignType(e);
    }

    const { getRootProps, getRadioProps } = useRadioGroup({
        name: 'framework',
        defaultValue: '지화1',
        onChange: handleDesignChange,
    })

    const group = getRootProps()

    return (
        <HStack {...group}>
            {options.map((value) => {
                const radio = getRadioProps({ value })
                return (
                    <RadioCard key={value} {...radio}>
                        {value}
                    </RadioCard>
                )
            })}
        </HStack>
    )
}