import JABA3 from "../assets/jaem/ㅂ-3.png"
import JASA3 from "../assets/jaem/ㅅ-3.png"
import JAAA3 from "../assets/jaem/ㅇ-3.png"
import JAJA3 from "../assets/jaem/ㅈ-3.png"
