import JAKA3 from "../assets/jaem/ㅋ-3.png"
import JATA3 from "../assets/jaem/ㅌ-3.png"
import JAPA3 from "../assets/jaem/ㅍ-3.png"
import JAHA3 from "../assets/jaem/ㅎ-3.png"
import JAGGA3 from "../assets/jaem/ㄲ-3.png"
import JADDA3 from "../assets/jaem/ㄸ-3.png"
import JABBA3 from "../assets/jaem/ㅃ-3.png"
import JASSA3 from "../assets/jaem/ㅆ-3.png"
import JAJJA3 from "../assets/jaem/ㅉ-3.png"


const Hangul = require('hangul-js');

//지화 그림 변환을 위한 Text 컴포넌트
const ParsedText = ({ text, styles, designType }) => {

    //지화 그림 1, 2, 3 이미지 정의
    let emoticons_1 = Object.freeze({
        "ㄱ": JAGA1,
        "ㄴ": JANA1,
        "ㄷ": JADA1,
        "ㄹ": JARA1,
        "ㅁ": JAMA1,
        "ㅂ": JABA1,
        "ㅅ": JASA1,
        "ㅇ": JAAA1,
        "ㅈ": JAJA1,
        "ㅊ": JACA1,
        "ㅋ": JAKA1,
        "ㅌ": JATA1,
        "ㅍ": JAPA1,
        "ㅎ": JAHA1,
        "ㄲ": JAGGA1,
        "ㄸ": JADDA1,
        "ㅃ": JABBA1,
        "ㅆ": JASSA1,
        "ㅉ": JAJJA1,
        "ㅏ": MOA1,
        "ㅑ": MOYA1,
        "ㅓ": MOUH1,
        "ㅕ": MOYUH1,
        "ㅗ": MOO1,
        "ㅛ": MOYO1,
        "ㅜ": MOWU1,
        "ㅠ": MOYU1,
        "ㅡ": MOEH1,
        "ㅣ": MOEE1,
        "ㅐ": MOAE1,
        "ㅒ": MOYAE1,
        "ㅔ": MOAAE1,
        "ㅖ": MOYAAE1,
        "ㅚ": MOWEH1,
        "ㅟ": MOWE1,
        "ㅢ": MOEI1,
    });
    let emoticons_2 = Object.freeze({
        "ㄱ": JAGA2,
        "ㄴ": JANA2,
        "ㄷ": JADA2,
        "ㄹ": JARA2,
        "ㅁ": JAMA2,
        "ㅂ": JABA2,
        "ㅅ": JASA2,
        "ㅇ": JAAA2,
        "ㅈ": JAJA2,
        "ㅊ": JACA2,
        "ㅋ": JAKA2,
        "ㅌ": JATA2,
        "ㅍ": JAPA2,
        "ㅎ": JAHA2,
        "ㄲ": JAGGA2,
        "ㄸ": JADDA2,
        "ㅃ": JABBA2,
        "ㅆ": JASSA2,
        "ㅉ": JAJJA2,
        "ㅏ": MOA2,
        "ㅑ": MOYA2,
        "ㅓ": MOUH2,
        "ㅕ": MOYUH2,
        "ㅗ": MOO2,
        "ㅛ": MOYO2,
        "ㅜ": MOWU2,
        "ㅠ": MOYU2,
        "ㅡ": MOEH2,
        "ㅣ": MOEE2,
        "ㅐ": MOAE2,
        "ㅒ": MOYAE2,
        "ㅔ": MOAAE2,
        "ㅖ": MOYAAE2,
        "ㅚ": MOWEH2,
        "ㅟ": MOWE2,
        "ㅢ": MOEI2,
    });
    let emoticons_3 = Object.freeze({
        "ㄱ": JAGA3,
        "ㄴ": JANA3,
        "ㄷ": JADA3,
        "ㄹ": JARA3,
        "ㅁ": JAMA3,
        "ㅂ": JABA3,
        "ㅅ": JASA3,
        "ㅇ": JAAA3,
        "ㅈ": JAJA3,
        "ㅊ": JACA3,
        "ㅋ": JAKA3,
        "ㅌ": JATA3,
        "ㅍ": JAPA3,
        "ㅎ": JAHA3,
        "ㄲ": JAGGA3,
        "ㄸ": JADDA3,
        "ㅃ": JABBA3,
        "ㅆ": JASSA3,
        "ㅉ": JAJJA3,
        "ㅏ": MOA3,
        "ㅑ": MOYA3,
        "ㅓ": MOUH3,
        "ㅕ": MOYUH3,
        "ㅗ": MOO3,
        "ㅛ": MOYO3,
        "ㅜ": MOWU3,
        "ㅠ": MOYU3,
        "ㅡ": MOEH3,
        "ㅣ": MOEE3,
        "ㅐ": MOAE3,
        "ㅒ": MOYAE3,
        "ㅔ": MOAAE3,
        "ㅖ": MOYAAE3,
        "ㅚ": MOWEH3,
        "ㅟ": MOWE3,
        "ㅢ": MOEI3,
    });

    //한글 자음 모음 분리 (grouped = true 일때 한 글자씩 분리, false = 한 음절씩 분리)
    const disAssembledText = Hangul.disassemble(text, true);
    //console.log(disAssembledText);

    //분리된 자음, 모음을 이미지로 변환하는 부분
    return disAssembledText.map(letter => {

        // ㅚ ㅟ ㅢ 별도 탐색을 위한 skip 변수
        let skip = false;

        // 한글자씩 탐색
        return letter.map((elem, index, array) => {
            
            // 음절이 비어있을 경우(마지막인 경우) 탐색 종료
            if (!elem) return null;

            // 전 음절에서 ㅚ ㅟ ㅢ 탐색을 마친 경우 이번 음절은 스킵 (모음)
            if(skip === true) {
                skip = false;
                return null;
                
            } else {
                
                // 이번 음절이 모음일 경우
                if(Hangul.isVowel(elem)) {
                    // 다음 음절이 비어있을 경우
                    if(!array[index+1]) {
                        // 계속 진행
                    } else {
                        // 다음 음절도 모음일 경우
                        if(Hangul.isVowel(array[index+1])) {
                            // 이번 음절 + 다음 음절이 ㅚ인지 확인
                            if(elem === "ㅗ" && array[index+1] === "ㅣ") {
                                elem = "ㅚ";
                                // 다음 음절 skip
                                skip = true;
                            // 이번 음절 + 다음 음절이 ㅟ인지 확인
                            } else if(elem === "ㅜ" && array[index+1] === "ㅣ") {
                                elem = "ㅟ";
                                // 다음 음절 skip
                                skip = true;
                            // 이번 음절 + 다음 음절이 ㅢ인지 확인
                            } else if(elem === "ㅡ" && array[index+1] === "ㅣ") {
                                elem = "ㅢ";
                                // 다음 음절 skip
                                skip = true;
                            }
                        }
                        //계속 진행
                    }
                }

                // 지화그림 디자인 종류에 따라 이미지 선택 (지화1 ~ 지화3)
                if(designType === "지화1") {
                    // 지화 변환 대상에 해당하는 경우
                    if (emoticons_1[elem]) {
                        // 해당하는 지화그림 이미지 리턴
                        return <img style={styles.emtotic} src={emoticons_1[elem]} />;
                    }
                    // 지화 변환 대상에 해당하지 않는 경우
                    // 일반 텍스트 리턴
                    return <span>{elem}</span>;
                } else if(designType === "지화2") {
                    // 지화 변환 대상에 해당하는 경우
                    if (emoticons_2[elem]) {
                        // 해당하는 지화그림 이미지 리턴
                        return <img style={styles.emtotic} src={emoticons_2[elem]} />;
                    }
                    // 지화 변환 대상에 해당하지 않는 경우
                    // 일반 텍스트 리턴
                    return <span>{elem}</span>;
                } else if(designType === "지화3") {
                    // 지화 변환 대상에 해당하는 경우
                    if (emoticons_3[elem]) {
                        // 해당하는 지화그림 이미지 리턴
                        return <img style={styles.emtotic} src={emoticons_3[elem]} />;
                    }
                    // 지화 변환 대상에 해당하지 않는 경우
                    // 일반 텍스트 리턴
                    return <span>{elem}</span>;
                } else {
                    // 지화 변환 대상에 해당하는 경우
                    if (emoticons_1[elem]) {
                        // 해당하는 지화그림 이미지 리턴
                        return <img style={styles.emtotic} src={emoticons_1[elem]} />;
                    }
                    // 지화 변환 대상에 해당하지 않는 경우
                    // 일반 텍스트 리턴
                    return <span>{elem}</span>;
                }
            }
        });
    })

}

