import {Button, Flex, Input} from "@chakra-ui/react";
import React, {useState} from "react";
import {
    Slider,
    SliderTrack,
    SliderFilledTrack,
    SliderThumb,
    SliderMark,
} from '@chakra-ui/react'
import ControlDesignRadio from "./ControlDesignRadio"
import WindowSizeSlider from "../not-used-components/WindowSizeSlider";

//폰트 30 -> 15 (1/2)
//그림 30 -> 90,96 (*3, *3.2)


export default function Control(props) {

    //폰드 사이즈 변경 함수
    const handleFontSizeChange = (e) => {

        //이미지 크기 유지
        let width = props.styles.emtotic.width;
        let height = props.styles.emtotic.height;

        //폰트 사이즈 변경
        let fontSize = e;

        //style 설정
        props.setStyles({
            emtotic: {width: width, height: height, },
            fonts: {fontSize: fontSize, }
        });
    };

    //이미지 사이즈 변경 함수
    const handleImageSizeChange = (e) => {

        //이미지 크기 변경
        let width = e;
        let height = e * 1.06;

        //폰트 사이즈 유지
        let fontSize = props.styles.fonts.fontSize;

        //style 설정
        props.setStyles({
            emtotic: {width: width, height: height, },
            fonts: {fontSize: fontSize, }
        });
    };

    return (
        <Flex w="100%" flexDir="column">
            <Flex w="100%" flexDir="row">
                <ControlDesignRadio designType={props.designType} setDesignType={props.setDesignType}></ControlDesignRadio>
            </Flex>

            <Flex w="100%" mt="4" flexDir="row">
                <Flex w="20%" flexDir="row" justifyContent="center"  alignItems="center" >
                    <span>글씨 크기</span>
                </Flex>
                <Slider aria-label='slider-ex-1' min={2} max={50} defaultValue={15} onChange={handleFontSizeChange}>
                    <SliderTrack>
                        <SliderFilledTrack />
                    </SliderTrack>
                    <SliderThumb />
                </Slider>
           </Flex>

            <Flex w="100%" mt="4" flexDir="row">
                <Flex w="20%" flexDir="row" justifyContent="center" alignItems="center">
                    <span>그림 크기</span>
                </Flex>
                <Slider aria-label='slider-ex-1' colorScheme='pink' min={3} max={300} defaultValue={90} onChange={handleImageSizeChange}>
                    <SliderTrack>
                        <SliderFilledTrack />
                    </SliderTrack>
                    <SliderThumb />
                </Slider>
            </Flex>
            
        </Flex>
    );
};