import React, { useEffect, useRef } from "react";
import { Avatar, Flex, Text } from "@chakra-ui/react";
import ParsedText from "../components/ParsedText"
import avatarImage3 from "../assets/logo/flexible logo gif11.png"
import avatarImage from "../assets/logo/profile.png"

const Messages = ({ messages, styles, designType }) => {
    const AlwaysScrollToBottom = () => {
        const elementRef = useRef();
        useEffect(() => elementRef.current.scrollIntoView());
        return <div ref={elementRef} />;
    };

    return (
        <Flex w="100%"
              h="80%"
              overflowY="scroll"
              flexDirection="column"
              p="3"
              sx={
                  {
                      '::-webkit-scrollbar':{
                          display:'none'
                      }
                  }
              }
              scrollbar-width="none">
            {messages.map((item, index) => {
                // 초기 메세지이고, 나의 메세지인 경우 (현재 초기 메시지 갯수 = 3)
                if (index < 3 && item.from === "me") {
                    return (
                        <Flex key={index} w="100%" justify="flex-end">
                            <Flex
                                bg="#4BD593"
                                color="white"
                                minW="100px"
                                maxW="360px"
                                my="2"
                                px="4"
                                py="3"
                                borderTopLeftRadius="25px"
                                borderTopRightRadius="0px"
                                borderBottomLeftRadius="25px"
                                borderBottomRightRadius="25px"
                                boxShadow="3px 3px 3px 3px #EEEEEE"
                                whiteSpace="normal"
                                flexWrap="wrap"
                                fontSize={styles.fonts.fontSize}
                            >
                                <Text styles={styles.fonts}>{item.text}</Text>
                            </Flex>
                        </Flex>
                    );
                // 나의 메세지인 경우
                } else if (item.from === "me") {
                    return (
                        <Flex key={index} w="100%" justify="flex-end">
                            <Flex
                                bg="#4BD593"
                                color="white"
                                minW="100px"
                                maxW="360px"
                                my="2"
                                px="4"
                                py="3"
                                borderTopLeftRadius="25px"
                                borderTopRightRadius="0px"
                                borderBottomLeftRadius="25px"
                                borderBottomRightRadius="25px"
                                boxShadow="3px 3px 3px 3px #EEEEEE"
                                whiteSpace="normal"
                                flexWrap="wrap"
                                fontSize={styles.fonts.fontSize}
                            >
                                <Text styles={styles.fonts}>{item.text}</Text>
                            </Flex>
                        </Flex>
                    );
                // 초기 메세지이고, 상대방의 메세지인 경우 (현재 초기 메시지 갯수 = 3)
                } else if (index < 3 && item.from === "computer") {
                    return (
                        <Flex key={index} w="100%">
                            <Avatar
                                name="Computer"
                                src={avatarImage}
                                bg="white"
                            ></Avatar>
                            <Flex
                                bg="white"
                                borderColor="gray.100"
                                borderTopLeftRadius="0px"
                                borderTopRightRadius="25px"
                                borderBottomLeftRadius="25px"
                                borderBottomRightRadius="25px"
                                boxShadow="3px 3px 3px 3px #EEEEEE"
                                color="black"
                                minW="100px"
                                maxW="360px"
                                my="2"
                                px="4"
                                py="3"
                                ml="15px"
                                whiteSpace="normal"
                                flexWrap="wrap"
                                fontSize={styles.fonts.fontSize}
                                _before={{
                                    content: '""',
                                    position: 'absolute',
                                    height: "0px",
                                    width: "0px",
                                    top: "60px",
                                    left: "-29px", /* 1px buffer for zooming problems while rendering*/
                                    backGroundColor: "black",
                                    borderWidth: "15px",
                                    borderColor: "transparent white transparent transparent",
                                    borderStyle: "solid",}}
                            >
                                <Text>{item.text}</Text>
                            </Flex>
                        </Flex>
                    );
                // 상대방의 메세지인 경우
                } else {
                    return (
                        <Flex key={index} w="100%">
                            <Avatar
                                name="Computer"
                                src={avatarImage}
                                bg="white"
                            ></Avatar>
                            <Flex
                                bg="white"
                                borderColor="gray.100"
                                borderTopLeftRadius="0px"
                                borderTopRightRadius="25px"
                                borderBottomLeftRadius="25px"
                                borderBottomRightRadius="25px"
                                boxShadow="3px 3px 3px 3px #EEEEEE"
                                color="black"
                                minW="100px"
                                maxW="360px"
                                my="2"
                                px="4"
                                py="3"
                                flexDir="column"
                                marginLeft="5px"
                                whiteSpace="normal"
                                flexWrap="wrap"
                                fontSize={styles.fonts.fontSize}
                            >
                                <>
                                    <Flex flexDir="row"  flexWrap="wrap" maxW="350px">
                                        <ParsedText text={item.text} styles={styles} designType={designType}/>
                                    </Flex>
                                </>
                            </Flex>
                        </Flex>
                    );
                }
            })}
            <AlwaysScrollToBottom />
        </Flex>
    );
};

