import { Flex } from "@chakra-ui/react";
import React, { useState } from "react";
import Divider from "../components/Divider";
import Footer from "../components/Footer";
import Header from "../components/Header";
import Messages from "../components/Messages";
import Control from "../components/Control";


const Hangul = require('hangul-js');

const Chat = () => {
    //시작시 기본 출력되는 메세지
    const [messages, setMessages] = useState([
        { from: "computer", text: "안녕하세요 소느로 수화 브랜드 입니다!" },
        { from: "me", text: "네 안녕하세요!" },
        { from: "computer", text: "네 반갑습니다! 한글을 입력하시면, 지화 그림으로 변환해드리겠습니다!" }
    ]);
    //메시지 입력 state
    const [inputMessage, setInputMessage] = useState("");
    //지화 그림 디자인 종류 state
    const [designType, setDesignType] = useState("지화1");
    // 그림 및 폰트 크기 style state
    const [styles, setStyles] = useState({
        emtotic: { width: 90, height: 96,},
        fonts: {fontSize: 17,}
    });
    //메세지창 전체 크기 조절 state
    //const [windowSize, setWindowSize] = useState(50);

    //입력된 메시지 처리 함수
    const handleSendMessage = () => {
        if (!inputMessage.trim().length) {
            return;
        }
        const data = inputMessage;

        setMessages((old) => [...old, { from: "me", text: data }]);
        setInputMessage("");

        setTimeout(() => {
            setMessages((old) => [...old, { from: "computer", text: data }]);
        }, 1000);
    };

    return (
        <Flex w="100%" h="100vh" justify="center" align="center" bg="#EBF4F7">
            <Flex w={["100%", "100%", "45%"]} h="90%" flexDir="column">
                <Header/>
                <Divider />
                <Messages messages={messages} styles={styles} designType={designType}/>
                <Divider />
                <Control styles={styles} setStyles={setStyles} designType={designType} setDesignType={setDesignType}/>
                <Divider />
                <Footer
                    inputMessage={inputMessage}
                    setInputMessage={setInputMessage}
                    handleSendMessage={handleSendMessage}
                />
            </Flex>
        </Flex>
    );
};

export default Chat;
